import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { MessagePage } from '../message/message';
import { MessageInterface } from '../../model/MessageInterface';
import { TopicInterface } from '../../model/TopicInterface';
import { AccountInterface } from '../../model/AccountInterface'

/**
 * Generated class for the NotificationsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-notifications',
  templateUrl: 'notifications.html',
})
export class NotificationsPage {
  message : MessageInterface;
  topic: TopicInterface;
  topics: TopicInterface[];
  messagePage = MessagePage;
  account : AccountInterface;


constructor(public navCtrl: NavController, public navParams: NavParams) {
    this.topics =[
      {
        topicID: "topicID1",
        topicTitle: "topicTitle1",
        needReply: 1,
        author:{
          username: "username1",
          avatar:{
            url: ""
          }
        },
        date : "date1",
        messages: [
          {
            author: {
              username : "emily",
              avatar : {
                url : "images/emily.png"
              }
            },
            content: "Hi, are you there?",
            date : "message1Topic1Time"
          },{
            author: {
              username: "daniel",
              avatar :{
                url : "images/daniel.png"
              }
            },
            content: "yygo dinner later?",
            date: "message2Topic1Time"
          },{
            author: {
              username: "myself",
              avatar: {
                url: "images/me.jpg"
              }
            },
            content: "See you after work ",
            date: "message3Topic1Time"
          }
        ]
      },
      {
        topicID: "topicID2",
        topicTitle: "topicTitle2",
        needReply: 0,
        author:{
          username: "username2",
          avatar:{
            url: "url2"
          }
        },
        date : "date2",
        messages: [
          {
            author: {
              username : "emily",
              avatar : {
                url : "images/emily.png"
              }
            },
            content: "yes",
            date : "message1Topic2Time"
            }
        ]
      }
    ]
}
ionViewDidLoad(){
  console.log('ionViewDidLoad NotificationsPage');
  this.account = this.navParams.get('account');
}

goMessagePage(topic,account){
// console.log("Ojbect: "+topic);
  this.navCtrl.push(this.messagePage, {
    topic: topic,
    account: account
  })
}

}



