import { Component} from '@angular/core';
// import {Page} from 'ionic-angular';
import { NavController } from 'ionic-angular';
import { NotificationsPage } from '../notifications/notifications';
import { AccountInterface } from '../../model/AccountInterface';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})


export class HomePage {
  notificationsPage = NotificationsPage;
  account : AccountInterface;
  authenticated : boolean;
  un: number;
  pwd : string;
  constructor(public navCtrl: NavController) {
    this.navCtrl = navCtrl;
    this.account = {
      accountID : 13,
      username : "myself",
      pwdSalt : "pwdSalt",
      pwdHash : "pwdHash",
      avatar: {
        url: "images/me.jpg"
      },
      type: "accountType",
      active: "A",
      date: "loginDate"
    }
    this.authenticated = false;
  }
  login(): void{
    if(this.un == this.account.accountID && this.pwd == this.account.pwdHash){
      this.authenticated = true;
    }
 	}
  goNotificationsPage(account): void{
    // console.log("Ojbect: "+topic);
    this.navCtrl.push(this.notificationsPage, {
       account: account
    })
  }
  logout() : void {
    this.authenticated = false;
  }
}



