import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

import { MessageInterface } from '../../model/MessageInterface';
import { TopicInterface } from '../../model/TopicInterface';
import { Keyboard } from '@ionic-native/keyboard';
import { AccountInterface } from '../../model/AccountInterface';


/**
 * Generated class for the MessagePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-message',
  templateUrl: 'message.html'
})



export class MessagePage {
  private topic: TopicInterface;
  private messages: MessageInterface[];
  private account : AccountInterface;
  constructor(public navCtrl: NavController, public navParams: NavParams, private keyboard : Keyboard) {
    this.navParams = navParams;
    this.keyboard = keyboard;
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad MessagePage');
    this.topic = this.navParams.get('topic');
    this.account = this.navParams.get('account');
    this.messages = this.topic.messages;
  }

  inputUp(): void {
    this.keyboard.show();
  }

  inputDown() : void {
    this.keyboard.close();
  }

  // sendMessage() : void{
  //   this.topic.messages.push(
  //   this.topic)
  // }

  closeKeyboard() : void {
    this.keyboard.close();
  }
}



